.mb-result-map{
      height: 58vh;
}
.mb-card-list{
      width: 100%;
      height: 42vh;
      background-color: var(--bgWhite);
      z-index: 4;
      padding: 24px;
      overflow-y: scroll;
}
.mb-card-list__list{
      display: flex;
}
.mb-card-list__list li{
      width: 216px;
      padding: 0px 10px;
}
.mb-card-list__item{
      box-shadow: 0px 6px 15px #DCD8EE;
      border-radius: 16px;
      width: 196px;
}
.mb-card-list__item .item-thumbnail{
      border-radius: 16px 16px 0px 0px;
      width: 100%;
      height: 123px;
      object-fit: cover;
}
.mb-card-list__item .item-cnt{
      padding: 8px 16px;
}
.mb-card-list__item .item-cnt__name{
      color: var(--purple);
      font-size: 14px;
      line-height: 20px;
      height: 40px;
      font-weight: 700;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      word-break: break-word;
}
.mb-card-list__item .item-cnt__address{
      margin: 4px 0px;
}
.mb-card-list__item .item-cnt__address,
.mb-card-list__item .item-cnt__time,
.mb-card-list__item .item-cnt__price{
      display: flex;
      justify-content: flex-start;
}
.mb-card-list__item .item-cnt__address img,
.mb-card-list__item .item-cnt__time img,
.mb-card-list__item .item-cnt__price img
{
      width: 16px;
      height: 16px;
}
.mb-card-list__item .item-cnt__address span{
      padding-left: 4px;
      font-weight: 600;
      font-size: 12px;
      line-height: 16px;
      height: 48px;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      word-break: break-word;
      color: var(--text-hover);
}
.mb-card-list__item .item-cnt__time span{
      padding-left: 4px;
      font-weight: 600;
      font-size: 12px;
      line-height: 16px;
      color: var(--text-hover);
}
.mb-card-list__item .item-cnt__price{
      align-items: center;
}
.mb-card-list__item .item-cnt__price span{
      font-size: 12px;
      line-height: 16px;
      font-weight: 700;
      padding-left: 4px;
      color: var(--purple);
}